class DateUtil {
    constructor(text, originVal, format, color) {
        this.text = text;
        this.originVal = originVal
        this.format = format;
        this.color = color;
    }

    static #query = [
        {
            key: '투캐릭터',
            text: `TO_CHAR(#val, '#formmater')`
        },
        {
            key: '투데이트',
            text: `TO_DATE(#val, 'YYYYMMDD')`
        },
        {
            key: '1일투데이트',
            text: `TO_DATE(#val || '01', 'YYYYMMDD')`
        },
        {
            key: '말일',
            text: `LAST_DAY(#val)`
        },
        {
            key: '1일시분초',
            text: `TO_DATE(#val || '01 23:59:59', 'YYYY-MM-DD HH24:MI:SS')`
        },
        {
            key: '시분초',
            text: `TO_DATE(#val || ' 23:59:59', 'YYYY-MM-DD HH24:MI:SS')`
        },
        {
            key: '애드먼스',
            text: `ADD_MONTHS(#val, 1)`
        },
        {
            key: '막대기없애기',
            text: `REPLACE(#val, '-', '')`
        },
        {
            key: '트렁크',
            text: `TRUNC(#val)`
        },
        {
            key: '콘캣',
            text: `CONCAT(#val, '01')`
        },
        {
            key: 'YYYYMM자르기',
            text: `SUBSTR(#val, 1, 6)`
        }
    ]

    //key로 쿼리 가지고오기
    static getQuery(key) {
        const item = this.#query.find((obj) => obj.key === key);
        return item ? item.text : null;
    }

    //쿼리 세팅
    static changeDate(key, val, originVal, formmater, color) {
        const text = this.getQuery(key).replace('#formmater', formmater);
        let hex = color;
        if(color == null) { //==는 null과 undefined를 동등하게 처리 ===는 타입 구분함
            const hue = Math.floor(Math.random() * 360);
            const saturation = 70 + Math.floor(Math.random() * 30); // 채도: 70% ~ 100%
            const lightness = 70 + Math.floor(Math.random() * 30); // 밝기: 70% ~ 100%

            // HSL을 RGB로 변환하여 HEX 코드로 변환
            const hslToHex = (h, s, l) => {
                s /= 100;
                l /= 100;
                const k = n => (n + h / 30) % 12;
                const a = s * Math.min(l, 1 - l);
                const f = n => l - a * Math.max(-1, Math.min(k(n) - 3, Math.min(9 - k(n), 1)));
                const toHex = x => Math.round(255 * x).toString(16).padStart(2, '0');
                
                const r = toHex(f(0));
                const g = toHex(f(8));
                const b = toHex(f(4));
                
                return `#${r}${g}${b}`;
            };

            hex = hslToHex(hue, saturation, lightness)
        }

        return new DateUtil(val, originVal, text, hex);
    }

    //스팬씌우기 체이닝 함수
    toSpan() {
        const parts = this.format.split('#val');
        const result = `<span style="color:${this.color}" onmouseover='this.style.backgroundColor="yellow"' onmouseleave='this.style.backgroundColor=""'>${parts[0]}</span>#val<span style="color:${this.color}" onmouseover='this.style.backgroundColor="yellow"' onmouseleave='this.style.backgroundColor=""'>${parts[1]}</span>`.replace('#val', this.text).replace(this.originVal, '#val');
        return result;
    }

    //플레인 텍스트 체이닝 함수
    toText() {
        return this.format.replace('#val', this.text).replace(this.originVal, '#val');
    }
}

export default DateUtil;